import { GET_DOGS, NEXT_PAGE } from "./action_types"

const initialState = {
    numPage: 1,
    dogs:[],
    dogsFav: {}
}

const reducer = (state = initialState, action) => {
    switch(action.type){
        case GET_DOGS: 
        return {
            ...state,
            dogs: action.payload
        }
        default: 
            return {...state}
        case NEXT_PAGE:
        return{
            ...state,
            numPage: state.numPage +1 
        } 
    }
}

export default reducer