import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getDogs } from "../../redux/actions";
import CardDog from "./CardDog";
import Paginate from "./Paginate";

const CardDogs = () => {
    const dispatch = useDispatch();

    const dogs = useSelector((state)=> state.dogs)
    const numPage = useSelector((state)=> state.numPage)


    let from = (numPage - 1) * 12;
    let until = numPage * 12

    let viewCharacters = dogs.slice(from,until);  
    let cantPages = Math.floor(dogs.length / 5); 


    useEffect(()=>{
        dispatch(getDogs())
    },[])

    return(
        <>
        <div>
            <h1>DOGS</h1>
            {
                viewCharacters.map((dog)=> {
                    return(
                        <CardDog 
                        key={dog.id}
                        id={dog.id}
                        nombre={dog.nombre}
                        image = {dog.image}
                        />
                        )
                    })
                }
        </div>
        <Paginate cantPages={cantPages}></Paginate>
        </>
    )
}

export default CardDogs